import { CollaboratorsRoutingModule } from './collaborators.routing.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { CollaboratorsComponent } from './collaborators.component';

import { ListColllaboratorsComponent } from './list-colllaborators/list-colllaborators.component';
import { AddCollaboratorComponent } from './add-collaborator/add-collaborator.component';
import { EditCollaboratorComponent } from './edit-collaborator/edit-collaborator.component';
import { FormsModule } from '@angular/forms';
import { CollaboratorsService } from './collaborators.service';

@NgModule(
    {
        imports: [
            CommonModule,
            FormsModule,
            CollaboratorsRoutingModule
        ],
        exports: [],
        declarations: [
            CollaboratorsComponent,
            ListColllaboratorsComponent,
            AddCollaboratorComponent,
            EditCollaboratorComponent
        ],
        providers: [
            CollaboratorsService
        ]
    }
)

export class CollaboratorsModule {};


