import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MaterialComponent } from './material.component';
import { ListMaterialsComponent } from './list-materials/list-materials.component';
import { AddMaterialsComponent } from './add-materials/add-materials.component';
import { EditMaterialsComponent } from './edit-materials/edit-materials.component';

import { MaterialRoutingModule } from './material.routing.module';
import { MaterialService } from './material.service';

@NgModule(
    {
        imports: [
            CommonModule,
            FormsModule,
            MaterialRoutingModule
            
        ],
        exports: [
            MaterialRoutingModule
        ],
        declarations: [
            MaterialComponent,
            ListMaterialsComponent,
            AddMaterialsComponent,
            EditMaterialsComponent
        ],
        providers: [
            MaterialService
        ]
    }
)

export class MaterialModule {};


