import { Component, OnInit } from '@angular/core';
import { CollaboratorsService } from '../collaborators.service';
import { TitleService } from 'app/utils/title.service';

@Component({
  selector: 'app-add-collaborator',
  templateUrl: './add-collaborator.component.html',
  styleUrls: ['./add-collaborator.component.scss']
})
export class AddCollaboratorComponent implements OnInit {
  
  collaborator = {
    matricula: "",
    nome: "",
    sobrenome: "",
    telefone: "",
    senha: "",
    status: "1",
    permissoes:[
      
    ]};

  options = [
    {nome:'criar', id: 1, checked:false},
    {nome:'editar', id: 2, checked:false},
    {nome:'excluir', id: 3, checked:false},
    {nome:'consultar', id:4, checked:false},
  ];

  lastCollaborator;

  constructor(private collaboratorsService: CollaboratorsService,  private titleService: TitleService) {
  
   }

  ngOnInit() {
    this.titleService.setTitle("Cadastrar colaborador");
  }


  cadastrarColaborador() {
    let elm = <HTMLElement>document.querySelector("#btnAction");
      elm.textContent = "Cadastrando...";
    
    this.collaborator.permissoes = this.selectedOptions();
    console.log(this.collaborator);

    this.collaboratorsService.cadastrar(this.collaborator).then(response => {
        console.log(response.json());
      elm.textContent = "CADASTRADO!";
      elm.style.background = "#000000";
      elm.style.color = "#FFFFFF";
      
      this.lastCollaborator = this.collaborator;


      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        console.log("error->", response);
      });
  }



  criarObjetoCollaborador(){
    this.collaborator = {
      "matricula": "",
      "nome": "",
      "sobrenome": "",
      "telefone": "",
      "senha": "",
      "status": "",
      "permissoes":[
        
      ]};
  }

  salvar(){
   console.log(this.collaborator);
   console.log(this.selectedOptions());
    
  }

  selectedOptions() { // right now: ['1','3']
    return this.options
              .filter(opt => opt.checked)
              .map(opt => opt)
              //.map(opt => opt.value)
  }

}



