import { Component, OnInit } from '@angular/core';
import { MaterialService } from './../material.service';
import { TitleService } from 'app/utils/title.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-materials',
  templateUrl: './edit-materials.component.html',
  styleUrls: ['./edit-materials.component.scss']
})
export class EditMaterialsComponent implements OnInit {
  
  id: any;
  isValido: boolean = false;
  isLoading: boolean = true;
  isSemConexao: boolean = false;

  material: any = {
    id: "",
    nome: "Carregando...",
    tipo: "Carregando...",
    descricao: "Carregando...",
  };


  lastCollaborator;

   constructor(private activatedRoute: ActivatedRoute, private titleService: TitleService, private materialService: MaterialService) {

    this.titleService.setTitle("Carregando material...");
    this.activatedRoute.params.subscribe(params => {
      this.id = params['id'];
     this.material.id = this.id ;
      });

   }

   ngOnInit() {
    this.consultarID(this.id);
  }


  consultarID(id) {
    
    let elm = <HTMLElement>document.querySelector("#retornoTxt");
    elm.textContent = "Carregando os material...";

    let btnAction = <HTMLElement>document.querySelector("#btnAction");
    btnAction.textContent = "Carregando...";

    this.materialService.buscarPorId(id)
      .then(response => {
        this.material = response.json();
        console.log(response.json());
        if (this.material.length == 0) {
          this.isValido = false;
          elm.textContent = "Material inválido.";
          this.titleService.setTitle("Material inválido.");
          btnAction.textContent = "Material inválido";
          btnAction.style.background = "#d63031";
        } else{
          if(this.material.id != null){
            elm.textContent = "Editando " + this.material.nome;
            this.titleService.setTitle("Editando " + this.material.nome);
            btnAction.textContent = "Editar material";
          } else{
            this.isValido = false;
            elm.textContent = "Dados inválidos.";
            this.titleService.setTitle("Dados inválidos.");
            btnAction.textContent = "Dados inválidos";
            btnAction.style.background = "#d63031";
          }
         
        }
        
        this.isLoading = false;
      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "Sem conexão!";
        elm.textContent = "Você está offline";
        this.titleService.setTitle("Você está offline");
        this.isSemConexao = true;
        this.isLoading = false;
      });
  }

  
  editar() {
    let elm = <HTMLElement>document.querySelector("#btnAction");
      elm.textContent = "Editando...";
      elm.style.background = "#d63031";
    
    console.table(this.material);

    this.materialService.editar(this.material).then(response => {
        console.log(response.json());
        elm.textContent = "EDITADO!";
        elm.style.background = "#000000";
        elm.style.color = "#FFFFFF";
        elm.style.cursor = "not-allowed";
        elm.title = "Já editado os dados!"
      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        elm.textContent = "TENTAR NOVAMENTE";
        console.log("error->", response);
      });
  }
}



