import { Component, OnInit } from '@angular/core';
import { CollaboratorsService } from '../collaborators.service';
import { TitleService } from 'app/utils/title.service';
import { PagemodifyService } from 'app/utils/pagemodify.service';

@Component({
  selector: 'app-list-colllaborators',
  templateUrl: './list-colllaborators.component.html',
  styleUrls: ['./list-colllaborators.component.scss']
})
export class ListColllaboratorsComponent implements OnInit {

  collaborators: any;
  items: string[];
  isSemMotorista: boolean = false;
  isSemConexao: boolean = false;
  qtCollaboradores = 0;
  collaboratorInModal: any = false;

  searchQuery: any = '';

  constructor(private collaboratorsService: CollaboratorsService, private titleService: TitleService,  private pagemodifyService: PagemodifyService) {

  }

  ngOnInit() {
    this.titleService.setTitle("Listagem colaboradores");
    this.consultarColaboradores();
    this.initializeItems();
  }

  ionViewDidLoad() {

  }

  initializeItems() {
    this.items = this.collaborators;
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    const val = ev.target.value;
    //const val = this.searchQuery;
console.log(val);
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item: any) => {
        let stringItems = JSON.stringify(item).toLowerCase();
        console.log(stringItems);
        if(stringItems.indexOf(val.toLowerCase()) > -1 ){
          return item;
        }

      })
    }
  }


  consultarColaboradores() {

    let elm = <HTMLElement>document.querySelector("#qtRegisterTable");
    elm.textContent = "Carregando os colaboradores...";


    this.collaboratorsService.listar()
      .then(response => {
        this.collaborators = response.json();
        this.items  = response.json();
        console.table(this.items);
        console.log(this.collaborators.length);
        if (this.collaborators.length == 0) {
          this.isSemMotorista = true;
        }

        elm.textContent = this.collaborators.length + " colaboradores na tabela";

      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "0 colaboradores na tabela";
        this.isSemConexao = true;
      });
  }

  editarView(id){
  //  this.pagemodifyService.setPage("/edit/"+id);
  }

  detalhar(item: any){
    this.collaboratorInModal = item;
    
    this.titleService.setTitle("Detalhando " + item.nome );
  }

  fecharDetalhar(){
    
    this.titleService.setTitle("Listagem colaboradores");
    this.collaboratorInModal = false;
  }

}



