import { OsService } from './../os.service';
import { Component, OnInit } from '@angular/core';
import { ProjectsService } from 'app/dashboard/projects/projects.service';
import { TitleService } from 'app/utils/title.service';
import { CollaboratorsService } from 'app/dashboard/collaborators/collaborators.service';

@Component({
  selector: 'app-add-os',
  templateUrl: './add-os.component.html',
  styleUrls: ['./add-os.component.scss']
})
export class AddOsComponent implements OnInit {

  projects: any;
  projectSelected: any;

  collaborators: any;
  collaboratorSelected: any;

  isSemConexao: boolean = false;
  CurrentTime = new Date();

  
  os: any = 
    {
    id:1,
    descricao:"",
    local:"",
    dataInicio: this.CurrentTime,
    dataFim: this.CurrentTime,
    status:"1",
    projeto: {
      id:1 
    },
    colaborador: {
      id:2
    }
    };

  constructor(private projectsService: ProjectsService, private titleService: TitleService, private collaboratorsService: CollaboratorsService, private osService: OsService) { }

  ngOnInit() {
    this.titleService.setTitle("Nova Ordem de serviços");
    this.listarProjetos();
    this.listarColaboradores();
  }

  listarProjetos() {

    let elm = <HTMLElement>document.querySelector("#txtRetorno");
    elm.textContent = "Carregando informações...";


    this.projectsService.listar()
      .then(response => {
        this.projects = response.json();
        console.log(this.projects.length);
        if (this.projects.length == 0) {
          //this.isSemMotorista = true;
        }

        elm.textContent = "Preencha os campos abaixo";

      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "Sem conexão, atualize a página.";
        this.isSemConexao = true;
      });
  }

  

  listarColaboradores() {

    this.collaboratorsService.listar()
      .then(response => {
        this.collaborators = response.json();
         if (this.collaborators.length == 0) {
        //  this.isSemMotorista = true;
        }

        
      })
      .catch(response => {
        console.error("error->", response);
        this.isSemConexao = true;
      });
  }


  exibirProjetoSelecionado(){
   
    this.os.projeto.id = this.projectSelected.id;
    this.os.colaborador.id = this.collaboratorSelected.id;
    
    console.table(this.os);
  }

  cadastrar() {
    this.os.projeto.id = this.projectSelected.id;
    this.os.colaborador.id = this.collaboratorSelected.id;

    let elm = <HTMLElement>document.querySelector("#btnAction");
    elm.textContent = "Cadastrando...";
    
    console.log(this.os);

    this.osService.cadastrar(this.os).then(response => {
      console.log(response.json());
      elm.textContent = "CADASTRADO!";
      elm.style.background = "#000000";
      elm.style.color = "#FFFFFF";
      
      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        console.log("error->", response);
      });
  }


}



