import { EditMaterialsComponent } from './edit-materials/edit-materials.component';
import { AddMaterialsComponent } from './add-materials/add-materials.component';
import { ListMaterialsComponent } from './list-materials/list-materials.component';
import { MaterialComponent } from './material.component';

import { NgModule } from "@angular/core";
import { Routes, RouterModule } from '@angular/router';


const DashboardRoutes: Routes = [
    { path: 'materials', component: MaterialComponent, children: [
        { path: 'list', component: ListMaterialsComponent },
        { path: 'add', component: AddMaterialsComponent },
        { path: 'edit/:id', component: EditMaterialsComponent },
        { path: 'delete/:id', component: MaterialComponent },
        { path: ':id', component: MaterialComponent },
    ]},
   
]

@NgModule({
    imports: [ RouterModule.forChild(DashboardRoutes) ],
    exports: [ RouterModule ],
})
export class MaterialRoutingModule {

}


