import { Component, OnInit } from '@angular/core';
import { AuthGuardService } from 'app/auth/authguard.service';
import { TitleService } from 'app/utils/title.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  user: any;
  titlePage: any;
  menu: boolean = false;

  constructor( private authGuardService: AuthGuardService, private titleService: TitleService) { }
    


  ngOnInit() {
    
    this.titleService.setTitle("Dashboard Ezentis");

    this.authGuardService.canActivate();

    this.user = JSON.parse(localStorage.getItem('currentUser'));
    
    console.table(this.user.name);
  }

  logout(){
    this.authGuardService.logout();
  }
  abrirMenu(opcao){
    let elm = <HTMLElement>document.querySelector("#menuMain");

   if(opcao == "abrir"){
    this.menu = true;
    elm.style.right = "260px";
   } else {
     
    if(elm.style.right == "260px"){
      elm.style.right = "0px";
     }

   }
    console.log(elm.style.right);
  
  }
}



