import { Component, OnInit } from '@angular/core';

import { MaterialService } from './../material.service';

import { TitleService } from 'app/utils/title.service';

@Component({
  selector: 'app-add-materials',
  templateUrl: './add-materials.component.html',
  styleUrls: ['./add-materials.component.scss']
})

export class AddMaterialsComponent implements OnInit {
  
  material = {
    nome: "",
    tipo: "",
    descricao: ""
  };


  lastCollaborator;

  constructor(private materialService: MaterialService, private titleService: TitleService) {
  
   }

  ngOnInit() {
    this.titleService.setTitle("Cadastrar material");
  }


  cadastrar() {
    let elm = <HTMLElement>document.querySelector("#btnAction");
      elm.textContent = "Cadastrando...";
    
    console.log(this.material);

    this.materialService.cadastrar(this.material).then(response => {
        console.log(response.json());
      elm.textContent = "CADASTRADO!";
      elm.style.background = "#000000";
      elm.style.color = "#FFFFFF";
      
      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        console.log("error->", response);
      });
  }


}



