import { TitleService } from './../utils/title.service';
import { Component, OnInit } from '@angular/core';
import { LoginService } from './login.service';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthGuardService } from 'app/auth/authguard.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  user: any;
  login: any;
  password: any;
  msgErro: boolean = false;

  constructor(
    
    private loginService: LoginService,
    private route: ActivatedRoute,
    private router: Router, 
    private authGuardService: AuthGuardService,
    private titleService: TitleService
    ) { }

 
  ngOnInit() {
    this.titleService.setTitle("Faça o login");
    
    if(this.authGuardService.canActivate()){
      this.router.navigate(['dashboard']);
    }
   }

  signin(){
    
    console.log(this.login);
    
    this.user = this.loginService.signin(this.login, this.password);
    
    if(this.user == null){
      this.msgErro = true;
    } else {
      this.router.navigate(['dashboard']);
    }

  }
}



