import { OsService } from './../os.service';
import { Component, OnInit } from '@angular/core';
import { TitleService } from 'app/utils/title.service';

@Component({
  selector: 'app-list-os',
  templateUrl: './list-os.component.html',
  styleUrls: ['./list-os.component.scss']
})
export class ListOsComponent implements OnInit {

   
  collaborators: any;
  items: string[];
  isSemMotorista: boolean = false;
  isSemConexao: boolean = false;
  qtCollaboradores = 0;
  objetoInModal: any = false;
  searchQuery: any = '';

  constructor(private titleService: TitleService, private osService: OsService) { }

  ngOnInit() {
    this.titleService.setTitle("Listagem de ordens de serviço.");
    this.consultarTodos();
    this.initializeItems();
  }

  ionViewDidLoad() {

  }

  initializeItems() {
    this.items = this.collaborators;
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializeItems();

    // set val to the value of the searchbar
    const val = ev.target.value;
    //const val = this.searchQuery;
console.log(val);
    // if the value is an empty string don't filter the items
    if (val && val.trim() != '') {
      this.items = this.items.filter((item: any) => {
        let stringItems = JSON.stringify(item).toLowerCase();
        console.log(stringItems);
        if(stringItems.indexOf(val.toLowerCase()) > -1 ){
          return item;
        }

      })
    }
  }


  consultarTodos() {

    let elm = <HTMLElement>document.querySelector("#qtRegisterTable");
    elm.textContent = "Carregando os registros...";


    this.osService.listar()
      .then(response => {
        this.collaborators = response.json();
        this.items  = response.json();
        console.table(this.items);
        console.log(this.collaborators.length);
        if (this.collaborators.length == 0) {
          this.isSemMotorista = true;
        }

        elm.textContent = this.collaborators.length + " registros na tabela";

      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "0 registros na tabela";
        this.isSemConexao = true;
      });
  }

  editarView(id){
  //  this.pagemodifyService.setPage("/edit/"+id);
  }

  detalhar(item: any){
    
    console.log(item);

    this.objetoInModal = item;
    
    this.titleService.setTitle("Detalhando OS " + item.id );
  }

  fecharDetalhar(){
    
    this.titleService.setTitle("Listagem de ordens de serviço");
    this.objetoInModal = false;
  }

}



