import { Component, OnInit } from '@angular/core';
import { ProjectsService } from 'app/dashboard/projects/projects.service';
import { TitleService } from 'app/utils/title.service';
import { CollaboratorsService } from 'app/dashboard/collaborators/collaborators.service';
import { OsService } from '../os.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-os',
  templateUrl: './edit-os.component.html',
  styleUrls: ['./edit-os.component.scss']
})
export class EditOsComponent implements OnInit {

  
  projects: any;
  projectSelected: any;

  collaborators: any;
  collaboratorSelected: any;

  CurrentTime = new Date();

  isSemConexao: boolean = false;
  isValido: boolean = true;
  isLoading: boolean = true;
  
  os: any = 
    {
    id:1,
    descricao:"",
    local:"",
    dataInicio: this.CurrentTime,
    dataFim: this.CurrentTime,
    status:"1",
    projeto: {
      id:1 
    },
    colaborador: {
      id:2
    }
    };

  constructor(private activatedRoute: ActivatedRoute, private projectsService: ProjectsService, private titleService: TitleService, private collaboratorsService: CollaboratorsService, private osService: OsService) { }

  ngOnInit() {
    this.titleService.setTitle("Editar Ordem de serviços");
    this.listarProjetos();
    this.listarColaboradores();
    this.activatedRoute.params.subscribe(params => {
      this.os.id = params['id'];
      this.consultarID(this.os.id);
    });
  }

  
  consultarID(id) {
    
    let elm = <HTMLElement>document.querySelector("#retornoTxt");
    elm.textContent = "Carregando os material...";

    let btnAction = <HTMLElement>document.querySelector("#btnAction");
    btnAction.textContent = "Carregando...";

    this.osService.buscarPorId(id)
      .then(response => {
        this.os = response.json();
        console.log(response.json());
        if (this.os.length == 0) {
          this.isValido = false;
          elm.textContent = "OS inválida.";
          this.titleService.setTitle("OS inválida.");
          btnAction.textContent = "OS inválida.";
          btnAction.style.background = "#d63031";
        } else{
          if(this.os.id != null){

            this.projectSelected = this.os.projeto;
            this.collaboratorSelected = this.os.colaborador;

            elm.textContent = "Editando OS: " + this.os.id;
            this.titleService.setTitle("Editando OS: " + this.os.id);
            btnAction.textContent = "Editar OS";
          } else{
            this.isValido = false;
            elm.textContent = "Dados inválidos.";
            this.titleService.setTitle("Dados inválidos.");
            btnAction.textContent = "Dados inválidos";
            btnAction.style.background = "#d63031";
          }
         
        }
        
        this.isLoading = false;
      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "Sem conexão!";
        elm.textContent = "Você está offline";
        this.titleService.setTitle("Você está offline");
        this.isSemConexao = true;
        this.isLoading = false;
      });
  }


  listarProjetos() {



    this.projectsService.listar()
      .then(response => {
        this.projects = response.json();
        console.log(this.projects.length);
        if (this.projects.length == 0) {
          //this.isSemMotorista = true;
        }

     
      })
      .catch(response => {
        console.error("error->", response);
        this.isSemConexao = true;
      });
  }

  

  listarColaboradores() {

    this.collaboratorsService.listar()
      .then(response => {
        this.collaborators = response.json();
         if (this.collaborators.length == 0) {
        //  this.isSemMotorista = true;
        }

        
      })
      .catch(response => {
        console.error("error->", response);
        this.isSemConexao = true;
      });
  }


  exibirProjetoSelecionado(){
   
    this.os.projeto.id = this.projectSelected.id;
    this.os.colaborador.id = this.collaboratorSelected.id;
    
    console.table(this.os);
  }

  editar() {
    this.os.projeto.id = this.projectSelected.id;
    this.os.colaborador.id = this.collaboratorSelected.id;

    let elm = <HTMLElement>document.querySelector("#btnAction");
    elm.textContent = "Editando...";
    
    console.log(this.os);

    this.osService.editar(this.os).then(response => {
      console.log(response.json());
      elm.textContent = "EDITADO!!";
      elm.style.background = "#000000";
      elm.style.color = "#FFFFFF";
      
      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        console.log("error->", response);
      });
  }
}



