import { ListMaterialsComponent } from './material/list-materials/list-materials.component';
import { EditMaterialsComponent } from './material/edit-materials/edit-materials.component';
import { AddMaterialsComponent } from './material/add-materials/add-materials.component';
import { MaterialComponent } from './material/material.component';
import { DashboardComponent } from './dashboard.component';
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from '@angular/router';
import { OverviewComponent } from './overview/overview.component';
import { CollaboratorsComponent } from './collaborators/collaborators.component';
import { ListColllaboratorsComponent } from './collaborators/list-colllaborators/list-colllaborators.component';
import { AddCollaboratorComponent } from './collaborators/add-collaborator/add-collaborator.component';
import { EditCollaboratorComponent } from './collaborators/edit-collaborator/edit-collaborator.component';
import { UsersComponent } from './users/users.component';
import { AddUserComponent } from './users/add-user/add-user.component';
import { EditUserComponent } from './users/edit-user/edit-user.component';
import { ListUserComponent } from './users/list-user/list-user.component';
import { ProjectsComponent } from './projects/projects.component';
import { OsComponent } from './os/os.component';
import { ListOsComponent } from './os/list-os/list-os.component';
import { AddOsComponent } from './os/add-os/add-os.component';
import { EditOsComponent } from './os/edit-os/edit-os.component';


const DashboardRoutes: Routes = [
    { 
        path: 'dashboard', component: DashboardComponent , children: [
        
         { path: 'overview', component: OverviewComponent},

        { path: 'collaborators', component: CollaboratorsComponent, children:[
            { path: 'list', component: ListColllaboratorsComponent },
            { path: 'add', component: AddCollaboratorComponent },
            { path: 'edit/:id', component: EditCollaboratorComponent },
            { path: 'delete/:id', component: CollaboratorsComponent },
            { path: ':id', component: CollaboratorsComponent },
        ]},

        { path: 'users', component: UsersComponent, children:[
            { path: 'add', component: AddUserComponent },
            { path: 'list', component: ListUserComponent },
            { path: 'edit/:id', component: EditUserComponent },
            { path: 'delete/:id', component: UsersComponent },
        ]},
        
        { path: 'projects', component: ProjectsComponent, children:[
            { path: 'add', component: ProjectsComponent },
            { path: 'edit/:id', component: ProjectsComponent },
            { path: 'delete/:id', component: ProjectsComponent },
            { path: ':id', component: ProjectsComponent },
        ]},

        { path: 'materials', component: MaterialComponent, children:[
            { path: 'list', component: ListMaterialsComponent },
            { path: 'add', component: AddMaterialsComponent },
            { path: 'edit/:id', component: EditMaterialsComponent },
            { path: 'delete/:id', component: EditMaterialsComponent },
            { path: ':id', component: EditMaterialsComponent },
        ]},

        { path: 'orderservices', component: OsComponent, children: [
            { path: 'list', component: ListOsComponent },
            { path: 'add', component: AddOsComponent },
            { path: 'edit/:id', component: EditOsComponent },
            { path: 'delete/:id', component: EditOsComponent },
        ]},

    ]
},
   
]

@NgModule({
    imports: [ RouterModule.forChild(DashboardRoutes) ],
    exports: [ RouterModule ],
})
export class DashboardRoutingModule {

}


