import { Injectable } from '@angular/core';

import { Headers, Http } from '@angular/http';


@Injectable()
export class OsService {
    url = "https://ezentis.herokuapp.com";
  constructor(
    public http: Http) { }

    cadastrar(objeto){

        console.table(objeto);
        let url = this.url +"/api/os";
        let data = JSON.stringify(objeto);
        
        console.log(data);

        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        //headers.append('x-access-token', this.token);
  
        return this.http.post(url, data, { headers: headers }).toPromise();
    }

    editar(objeto){

      console.table(objeto);
      let url = this.url +"/api/os";
      let data = JSON.stringify(objeto);
      
      console.table(data);

      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
      //headers.append('x-access-token', this.token);

      return this.http.put(url, data, { headers: headers }).toPromise();
  }
    listar(){

      let url = this.url +"/api/os";
      let headers = new Headers();
      headers.append('Content-Type', 'application/json');
      //headers.append('x-access-token', this.token);

      return this.http.get(url, { headers: headers }).toPromise();
  }

  buscarPorId(id){

    let url = this.url +"/api/os/" + id;
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    //headers.append('x-access-token', this.token);

    return this.http.get(url, { headers: headers }).toPromise();
}


}


