import { ProjectsService } from './../projects/projects.service';
import { OsComponent } from './os.component';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ListOsComponent } from './list-os/list-os.component';
import { EditOsComponent } from './edit-os/edit-os.component';
import { AddOsComponent } from './add-os/add-os.component';

import { OsService } from './os.service';
import { OsRoutingModule } from './os.routing.module';


@NgModule(
    {
        imports: [
            CommonModule,
            FormsModule,
            OsRoutingModule
            
        ],
        exports: [
            OsRoutingModule
        ],
        declarations: [
            AddOsComponent,
            EditOsComponent,
            ListOsComponent,
            OsComponent
        ],
        providers: [
            OsService,
            ProjectsService
        ]
    }
)

export class OsModule {};


