import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TitleService } from 'app/utils/title.service';
import { CollaboratorsService } from '../collaborators.service';

@Component({
  selector: 'app-edit-collaborator',
  templateUrl: './edit-collaborator.component.html',
  styleUrls: ['./edit-collaborator.component.scss']
})
export class EditCollaboratorComponent implements OnInit {
  isValido: boolean = false;
  isLoading: boolean = true;
  isSemConexao: boolean = false;

  collaborator: any = {
    matricula: "Carregando...",
    nome:  "Carregando...",
    sobrenome:  "Carregando...",
    telefone: "Carregando...",
    senha: "Carregando...",
    status: "Carregando...",
    permissoes:[
      
    ]};

    id: any;

    options = [
      {nome:'Criar', id:'1', checked:false},
      {nome:'Editar', id:'2', checked:false},
      {nome:'Deletar', id:'3', checked:false},
      {nome:'Consultar', id:'4', checked:false},
    ];

  constructor(private activatedRoute: ActivatedRoute, private titleService: TitleService, private collaboratorsService: CollaboratorsService) {

    this.titleService.setTitle("Carregando colaborador...");
    this.activatedRoute.params.subscribe(params => {
      this.id = params['id'];
     
      });

   }

  ngOnInit() {
    this.consultarColaborador(this.id);
  }

selectedOptions() { // right now: ['1','3']
    return this.options
              .filter(opt => opt.checked)
              .map(opt => opt)
              //.map(opt => opt.value)
  }

  consultarColaborador(id) {
    
    let elm = <HTMLElement>document.querySelector("#retornoTxt");
    elm.textContent = "Carregando os colaboradores...";

    let btnAction = <HTMLElement>document.querySelector("#btnAction");
    btnAction.textContent = "Carregando...";

    this.collaboratorsService.buscarPorId(id)
      .then(response => {
        this.collaborator = response.json();
        console.log(response.json());
        if (this.collaborator.length == 0) {
          this.isValido = false;
          elm.textContent = "Colaborador inválido.";
          this.titleService.setTitle("Colaborador inválido.");
          btnAction.textContent = "Colaborador inválido";
          btnAction.style.background = "#d63031";
        } else{
          if(this.collaborator.id != null){
            elm.textContent = "Editando " + this.collaborator.nome;
            this.titleService.setTitle("Editando " + this.collaborator.nome);
            btnAction.textContent = "Editar colaborador";
            this.checarMarcados();
          } else{
            this.isValido = false;
            elm.textContent = "Colaborador inválido.";
            this.titleService.setTitle("Colaborador inválido.");
            btnAction.textContent = "Colaborador inválido";
            btnAction.style.background = "#d63031";
          }
         
        }
        
        this.isLoading = false;
      })
      .catch(response => {
        console.error("error->", response);
        elm.textContent = "Sem conexão!";
        elm.textContent = "Você está offline";
        this.titleService.setTitle("Você está offline");
        this.isSemConexao = true;
        this.isLoading = false;
      });
  }

  
  editarColaborador() {
    let elm = <HTMLElement>document.querySelector("#btnAction");
      elm.textContent = "Editando...";
      elm.style.background = "#d63031";
    
    this.collaborator.permissoes = this.selectedOptions();
    console.table(this.collaborator);

    this.collaboratorsService.editar(this.collaborator).then(response => {
        console.log(response.json());
        elm.textContent = "EDITADO!";
        elm.style.background = "#000000";
        elm.style.color = "#FFFFFF";
        elm.style.cursor = "not-allowed";
        elm.title = "Já editado os dados!"
      }).catch(response => {
        if (response.status == 403 || response.status == "403") {
          //this.attMotorista(this.driver.imei);
        }
        elm.textContent = "TENTAR NOVAMENTE";
        console.log("error->", response);
      });
  }

  checarMarcados(){
    for (let index = 0; index < this.options.length; index++) {
      const permissoesIntefaces = this.options[index];
      console.log("A " + permissoesIntefaces.nome );
      for (let index = 0; index < this.collaborator.permissoes.length; index++) {
        const permissoesBanco = this.collaborator.permissoes[index];
        
        if(permissoesIntefaces.id == permissoesBanco.id){
          permissoesIntefaces.checked = true;
          console.log("b " + permissoesIntefaces.nome );
        }

      }
      
    }
  }

}



